*,
*::before,
*::after {
	box-sizing: border-box;
}
ul,
ol {list-style: none;margin: 0;padding: 0;}
ul ul,
ol ul,
ul ol,
ol ol {
		list-style: none;
		margin: 0;padding: 0;
}
a, li, button, input, textarea {
				outline: none !important;
}
.clear {
				clear: both;
}

html, body {
	box-sizing: border-box;
}
html, body {
	min-width:320px;
}
body {
	color: $color-black;
	font-family: $font-family-Roboto;
	font-size: 18px;
	line-height: 36px;
	font-weight: 400;
	position: relative;
	width: 100%;
	-webkit-text-size-adjust: none;
	min-width: 320px;
	background: $color-white;
}
h1, .h1 {
	color: $color-black;
	font: 500 48px/52px $font-family-Roboto;
	margin: 0 0 35px;
	
	@media(max-width: 1279px) {
			font-size: 28px;
			line-height: 36px;
			margin: 0 0 10px;
	}
	@media(max-width: 767px) {
			font-size: 24px;
			line-height: 30px;
			margin: 0 0 10px;
	}

}
h2, .h2 {
	color: $color-black;
	font: bold 36px/40px $font-family-Roboto;
	margin: 0;
	
	@media(max-width: 1279px) {
			font-size: 24px;
			line-height: 26px;
	}	
	@media(max-width: 767px) {
			font-size: 18px;
			line-height: 20px;
	}	
}
p {
		color: $color-black;
		font: 18px/36px $font-family-Roboto;   
		margin: 0 0 20px;
		-webkit-text-size-adjust: none;

	a {
	color: $color-black;
	
		&:hover {
			text-decoration: underline;
		}
	}

}
a {
	-webkit-text-decoration-skip: objects;
	color: $color-black;
	text-decoration: none;   
}
.container {
	width: 100%;
	display: block;
	float: left;
	
	@media(min-width: 768px) {
		width: 768px;
		margin: 0 auto;
		float: none;
	}
	@media(min-width: 1280px) {
		width: 1240px;
		margin: 0 auto;
		float: none;
	}
}

svg:not(:root) {
		overflow: hidden;
}
button,
html [type="button"], 
[type="reset"],
[type="submit"] {
		-webkit-appearance: button; 
}
button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
		border-style: none;
		padding: 0;
}
[type="checkbox"],
[type="radio"] {
		box-sizing: border-box;
}
::-webkit-file-upload-button {
		-webkit-appearance: button; 
		font: inherit; 
}
.img-responsive {
	display: block;
	max-width: 100%;
	height: auto;
}
.text-uppercase {
	text-transform: uppercase;
}
.text-center {
	text-align: center;
}
.before {
	content: "";
	display: block;
	position: absolute;
}
.d-block {
	display: block;
	float: left; 
	width: 100%;    
}
