@import "variables";
@import "functions";
@import "mixins";
@import "sprites";
@import "fonts";
@import "vendor";
@import "base";


.list-index {
 background:#0a2743 !important;
 height: 100vh;
 padding: 20px 0 0;
 .list__page{
	text-align: center;
	margin: 30px 0 0;
	list-style: none;
	padding: 0;

	li {
	 margin: 0 0 10px;
	 a {
		color:#fff;
		font-size: 25px;
	 }
	}
 }
 h1{
	margin: 0;
	color:#fff !important;
	text-align: center;
 }
} //end .page-list-index
header {
	@extend .d-block; 
	background: $color-grey;
}
.header__top {
	position: fixed;
	z-index: 100;
	left: 0;
	top: 0;
	width: 100%;
	transition: background 0.2s;
	
	.container {
		border-bottom: 1px solid rgba($color-white,.4);		
		transition: border-color 0.2s;
	}

	&.headerFix {
		background: $color-blue6;
		
		.container {
			border-bottom-color: $color-blue6;
		}
	}
}
.header-top-content {
	display: flex;
	width: 100%;
	align-items: center;
}
.logo {
	
	a {
		display: block;
		float: left;
		height: 50px;
		text-decoration: none;
	}
}
.nav-container {
	display: flex;
	flex-grow: 1;
	height: 70px;	
}
.nav-content {
	display: block;
	width: 100%;
	height: 100%;
}
.nav {
	display: flex;
	height: 100%;
	
	li {
		height: 100%;
		
		a {
			display: flex;
			align-items: center;
			height: 100%;
			color: $color-white;
			font: 18px/20px $font-family-Roboto;
			padding-right: 1rem;
			padding-left: 1rem;
			text-decoration: none;
			text-align: center;
			justify-content: center;
			
			&:hover {
				text-decoration: underline;
			}
		}
	}
}

.header__content {
	height: 100vh;
	background: url($img_dir+"header-bg-1920.jpg") no-repeat top center;
	background-size: cover;
	position: relative;
	display: flex;
	align-items: flex-end;
	overflow: hidden;
	padding: 0 0 107px;
	transition: padding 0.2s;
	
	
	&::before {
		@extend .before;
		width: 100%;
		height: 100%;
		position: absolute;
		left: 0;
		top: 0;
		z-index: 5;
		background: rgba($color-black,.3);
	}
}
.header-box {
	display: flex;
	flex-direction: column;
	align-items: center;
	position: relative;
	z-index: 10;
	
	.btn {
		padding: 0 42px 0 36px;
		display: flex;
		align-items: center;
		margin-bottom: 58px;
		transition: margin 0.2s;
		
		.icon-reg {
			margin-right: 13px;
		}
	}
	
	h1, .h1 {
		color: $color-white;
		transition: margin 0.2s, font-size 0.2s;
		text-align: center;
	}

}

@media(min-width: 1280px) and (max-height: 500px)  {
	.header__content { padding: 0; }
	h1, .h1 {
		margin: 0 0 10px;
	}
	.header-box .btn {
		margin-bottom: 10px;
	}
}//- end media

.wrapper {
	@extend .d-block; 
}
section {
	@extend .d-block; 
}
.section-two {
	@extend .d-block; 
	position: relative;
	
	&::before {
		@extend .before;
		left: 0;
		top: 0;
		width: 50%;
		background: $color-white;
		height: 100%;
		z-index: 1;
	}
	&::after {
		@extend .before;
		right: 0;
		top: 0;
		width: 50%;
		background: $color-blue5;
		height: 100%;
		z-index: 1;
	}

	.container {
		position: relative;
		z-index: 10;
	}
}
.section-two {
	p {
		font-size: 24px;
		line-height: 36px;
		margin: 126px 0;
		
		a {
			font-weight: bold;
			color: $color-blue5;
		}
	}

	.btn {
		display: inline-block;
	}

}
.btn-sroll {
	position: absolute;
	left: 50%;
	top: -42px;
	width: 84px;
	height: 84px;
	text-decoration: none;
	z-index: 15;
	margin-left: -42px;
	display: flex;
	align-items: center;
	justify-content: center;
	
	span {
		width: 40px;
		height: 40px;
		border-radius: 20px;
		background: $color-white;
		display: flex;
		align-items: center;
		justify-content: center;
		box-shadow: 0 4px 12px 1px rgba(10,102,144,.25);
	}

	&::before {
		@extend .before;
		width: 84px;
		height: 84px;
		border-radius: 42px;
		box-shadow: 0 4px 5px 0 rgba(10,102,144,.23), 0 0 7px rgba(255,255,255,.35);
		border: 1px solid $color-white;
		left: 0;
		top: 0;
	}
	&::after {
		@extend .before;
		border: 1px solid $color-white;
		width: 60px;
		height: 60px;
		border-radius: 30px;
		border: 1px solid $color-white;
		box-shadow: 0 4px 5px 0 rgba(10,102,144,.23), 0 0 7px rgba(255,255,255,.35);
		left: 12px;
		top: 12px;
	}
}


.section-about {
	background: url($img_dir+"about-bg-1920.jpg") no-repeat top center;
	background-size: cover;
	min-height: 725px;
	position: relative;
	
	&::before {
		@extend .before;
		background: rgba($color-black,0.3);
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		z-index: 1;
	}
}
.about-content {
	display: flex;
	width: 100%;
	height: 100%;
	flex-direction: column;
	position: relative;
	z-index: 10;
	align-items: center;
	padding: 90px 0 0;
	
	h2 {
		color: $color-white;
		margin-bottom: 205px;
	}
	.btn {
		padding: 0 75px;
	}
}
.about-popup {
	display: none;
	position: absolute;
	left: 0;
	top: 0;
	background: rgba($color-black,0.7);
	width: 100%;
	height: 100%;
	z-index: 20;
}
.about-popup-content {
	display: flex;
	width: 100%;
	min-height: 100%;
	align-items: center;
	justify-content: center;
	padding: 20px 0;
	
	p {
		color: $color-white;
		line-height: 24px;
	}
}
// video
.video__box {
	position: relative;
	width: 100%;
	height: 0;
	padding-bottom: 56.25%;
	background-color: #000000;
	overflow: hidden;
}
.video__link {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	
	&::before {
		@extend .before;
		background: rgba($color-black,0.2);
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		z-index: 2;
	}
}
.video__media {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	border: none;
}
.video__button {
	position: absolute;
	top: 50%;
	left: 50%;
	z-index: 1;
	display: none;
	padding: 0;
	width: 68px;
	height: 48px;
	border: none;
	background-color: transparent;
	transform: translate(-50%, -50%);
	cursor: pointer;
}
.video__button-shape {
	fill: #ff0000;
	fill-opacity: 1;
}
.video__button-icon {
	fill: #ffffff;
}
.video__button:focus {
	outline: none;
}
/* Enabled */
.video--enabled {
	cursor: pointer;
}
.video--enabled .video__button {
	display: block;
}
// end video
.video__box-title {
	font: 18px/22px $font-family-Roboto;
	color: $color-black;
	display: block;
	width: 100%;
	margin-top: 15px;
}
.sub-title {
	width: 100%;
	text-align: center;
	font: 24px/45px $font-family-Roboto;
}
.video-content {
	display: block;
	width: 100%;
	margin-top: 52px;
	margin-bottom: 44px;
}
.section-video {
	padding: 82px 0 102px;
	
	h2, .h2 {
		margin-bottom: 11px;
	}
}
.section-thesis {
	background: $color-grey2;
	padding: 82px 0 116px;
	
	h2, .h2 {
		margin-bottom: 19px;
	}	
}
.thesis__box {
	width: 100%;
	display: flex;
	flex-direction: column;
	margin-top: 33px;
}
.thesis-title {
	display: flex;
	width: 100%;
	align-items: center;
	color: $color-blue7;
	font: 500 24px/26px $font-family-Roboto;
	margin-bottom: 24px;
	
	span {
		font: 900 100px/100px $font-family-Roboto;
		margin-right: 37px;
	}
}
.thesis-content {
	position: relative;
	padding-left: 72px;
	
	&::before {
		@extend .before;
		height: 100%;
		width: 2px;
		height: calc(100% - 23px);
		top: 11px;
		left: 46px;
		background: $color-blue8;
	}
	
	p {
		margin: 0;
	}
}

footer {
	@extend .d-block; 
	background: $color-blue5;
	padding: 28px 0 17px;
}

.footer-social {
	display: flex;
	align-items: center;
	width: 100%;
	justify-content: center;
	
	li {
		
		a {
			padding: .25rem .75rem;
			color: $color-grey3;
			transition: color 0.2s;

			&:hover {
				color: $color-white;
			}
		}
	}
}
.footer-copyright {
	color: $color-grey3;
	font-weight: bold;
	font-size: .875rem;
	text-align: center;
	line-height: 16px;
	
	a {
		color: rgba($color-white, .9);
		transition: color 0.2s;
		
		&:hover {
			color: $color-white;
		}
	}
}

.footer-contacts {
	display: flex;
	width: 100%;
	flex-direction: column;
	padding: 0 0 0 73px;
	
	li {
		width: 100%;
		line-height: 16px;
		margin: 5px 0;
		
		a {
			color: $color-grey3;
			font-size: 1rem;
			transition: color 0.2s;
			line-height: 16px;
			
			&:hover {
				color: $color-white;
			}
		}

		i {
			margin-right: .5rem;
		}
	}

	.fas.fa-phone {
		transform: rotate(90deg);  
	}
}
.col-hidden {
	display: none;
}
.video-btn-action {
	margin-top: 40px;
}
.nav-close, .touch-nav, .btn-video-show {
	display: none;
}

